@import url(https://fonts.googleapis.com/css2?family=Annie+Use+Your+Telescope&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}*,::after,::before{box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.container{width:100%;padding-left:15px;padding-right:15px}@media (min-width: 992px){.container{width:800px;margin:0 auto}}.container .row{margin-left:-15px;margin-right:-15px}body,html{width:100%;min-height:100%}html{scroll-behavior:smooth}body{font-size:1rem;font-family:'Annie Use Your Telescope', cursive;font-weight:400;color:#3a3a3a}*{-webkit-tap-highlight-color:transparent}a{text-decoration:none}h1,h2,h3,h4{padding-bottom:15px}h1{font-size:32px;font-weight:700}h2{font-size:28px;font-weight:700}h3{font-size:24px;font-weight:700}ul.list-inline li{display:inline-block}.capitalize{text-transform:capitalize}.bold{font-weight:700}

